function mainSlider() {
    var swiper = new Swiper('.main.swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        autoplay: {
          delay: 4000
        },
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        pagination: {
            el: '.main .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

let productSlidersArray = []

function productSliders() {
    $(".product-card").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        productSlidersArray[index] = new Swiper(`.product-card[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: 1,
            spaceBetween: 0,
            loop: true,
            navigation: {
                nextEl: `.product-card[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.product-card[data-slider-id="${index}"] .swiper-button-prev`,
            },
        })
    })
}

function blogSlider() {
    var swiper = new Swiper('.blog .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 30,
        breakpoints: {
            320: {
                slidesPerView: "auto",
                spaceBetween: 20,
            },
            992: {
                slidesPerView: 3,
                spaceBetween: 30,
            }
        }
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        spaceBetween: 30,
        direction: 'vertical',
        slidesPerView: 2,
        freeMode: true,
        watchSlidesProgress: true,
        navigation: {
            nextEl: `.gallery .swiper-button-next`,
            prevEl: `.gallery .swiper-button-prev`,
        },
        breakpoints: {
            320: {
                slidesPerView: "auto",
                spaceBetween: 7,
                direction: 'horizontal',
            },
            700: {
                slidesPerView: 3,
                direction: 'horizontal',
                spaceBetween: 7,
            },
            992: {
                spaceBetween: 30,
                direction: 'vertical',
                slidesPerView: 2,
            }
        }
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        direction: 'vertical',
        thumbs: {
            swiper: swiper,
        },
        breakpoints: {
            320: {
                direction: 'horizontal',
            },
            992: {
                direction: 'vertical',
            }
        }
    });
}

function modificationSlider() {
    var swiper = new Swiper('.modification .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 20,
        navigation: {
            nextEl: '.modification .swiper-button-next',
            prevEl: '.modification .swiper-button-prev',
        },
        breakpoints: {
            320: {
                spaceBetween: 9,
            },
            992: {
                spaceBetween: 20,
            },
        }
    })
}


mainSlider()
productSliders()
blogSlider()
gallery()
modificationSlider()
