if(window.innerWidth > 992){
    $(".nav__show").mouseenter(function () {
        $(this).parents(".nav__item").addClass("nav__item--active")
    })

    $(".nav__link").mouseenter(function () {
        $(".nav__item").removeClass("nav__item--active")
    })

    $(".header").mouseleave(function () {
        $(".nav__item").removeClass("nav__item--active")
    })
} else{
    $(".nav__show").click(function () {
        $(this).siblings(".nav__hidden").slideToggle()
    })
}



$(".header__search-toggle").click(function () {
    $(".header__search").toggleClass("_active")
})


$(document).mouseup(function (e){
    let div = $('.header');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        $(".header__search").removeClass("_active")
    }
});

$(".header__burger").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})
